@use '../base/variables' as var;
.read{
    width: 100%;
    height: 90%;
    padding-top: .5rem;
    display: flex;
    justify-content: center;
    font-family: 'Roboto', sans-serif;

    &__add{
        display: inline-block;
        text-decoration: none;
        padding: .5rem 1rem;
        background-color: var.$naranja;
        margin: .5rem;
        color: var.$blanco;
    }
}
table {
    width: 100%;
    border-collapse: collapse;
    margin:  0 auto;

    
}

.table__title{
    text-align: center;
    padding: .5rem 0;
    color: var.$blanco;
}

.header_fixed {
    max-height: 100vh;
    width: 100%;
    overflow: auto;
    // border: 1px solid #dddddd;
}

.header_fixed thead th {
    position: sticky;
    top: 0;
    background-color: var.$verde;
    color: #e6e7e8;
    font-size: 15px;
}

th,
td {
    border-bottom: 1px solid #dddddd;
    padding: 10px 20px;
    font-size: 14px;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 60px;
    font-size: 1rem;
}

td img {
    height: 5rem;
    // width: 3.75rem;
    // border-radius: 100%;
    border: 5px solid #e6e7e8;
    object-fit: cover;
    min-width: 100%;
}

tr:nth-child(even) {
    
    background-color: #dddddd;
}

tr:nth-child(odd) {
    background-color: #edeef1;
    min-width: 100%;
}

tr:hover td {
    color: var.$naranja;
    cursor: pointer;
    background-color: #ffffff;
}

td button {
    border: none;
    padding: 7px 20px;
    width: 48%;
    border-radius: 20px;
    background-color: var.$verde;
    color: #e6e7e8;
    // margin: .5rem;
}


.table-btn{

    border-bottom: 1px solid #dddddd;
    // padding: 10px 20px;
    font-size: 14px;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 100%;
    
    &__upd{
        display: block;
        width: 100%;
        // padding: .3rem;
        text-align: center;
        font-size: .7rem;
        cursor: pointer;
        text-decoration: none;
        padding: .5rem 0rem;
        background-color: var.$verde;
        color: var.$blanco;
        margin: .1rem 0;
    }

    &__del{
        display: block;
        width: 100%;
        background-color: rgb(107, 13, 13);
        font-size: .7rem;
        cursor: pointer;
        text-decoration: none;
        padding: .5rem 0rem;
        text-decoration: none;
        color: var.$blanco;
    }

}


::-webkit-scrollbar {
    width: 6px;
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

@media screen and(max-width:1000px) {
    .read__add{
        display: inline-block;
        align-self: flex-end;
    }
}