@use '../base/variables' as var;

.chuquis__title{
	// display: block;
	text-align: center;
	margin-top: 1rem;
	background-color: var.$verde;
	padding: 3rem 0;
	color: var.$blanco;
	font-family: var.$bold;
	font-size: 1.5rem;
}

/* --- --- CAROUSEL --- --- */

.chuquis-categoria{
	
	margin: 1rem;
}
.carousel__contenedor {
	width: 100%;
	// height: 18.75rem;
	position: relative;
}

.carousel__anterior,
.carousel__siguiente {
	position: absolute;
	display: block;
	width: 40px;
	height: 40px;
	border: none;
	top: calc(50% - 15px);
	cursor: pointer;
	// line-height: 30px;
	text-align: center;
	// z-index: 40;
	background: none;
	color: #fff;
	// opacity: 20%;
	fill: rgba(218, 208, 208, 0.466);
	// filter: drop-shadow(0px 0px 3px rgba(0, 0, 0, 0.459));
}

.carousel__anterior:active,
.carousel__siguiente:active{
	fill: #fff;
}

.carousel__anterior {
	left: 5px;
}

.carousel__siguiente {
	right: 5px;
}

.carousel__lista {
	overflow: hidden;
	height: 100%;
}

.carousel__elemento {
	text-align: center;
	margin: 0 5px;
	position: relative;
}

.carousel__indicadores .glider-dot {
	display: block;
	width: 30px;
	height: 4px;
	background: #fff;
	opacity: .2;
	border-radius: 0;
}

.carousel__parrafo{
	position: absolute;
	
	// top: calc(58% - 20px );
	bottom: 0;
	width: 100%;
	min-height: 40%;
	color: var.$blanco;
	font-family: var.$bold;
	background-color: rgba(0, 0, 0, 0.479);
	padding: .5rem 0;
	display: flex;
	flex-direction: column;
	align-items: center;

}

.carousel__title{
	font-size: 1.2rem;
	
}
.carousel__text{
	font-size: .9rem;
	font-family: var.$medium;
	padding: .6em;
	margin: 0;
}

.carousel__link{
	background-color: var.$naranja;
	text-decoration: none;
	font-family: var.$regular;
	font-size: .9rem;
	padding: .5em 1em;
	color: black;
	// margin-bottom: .6em;
}
.carousel__indicadores .glider-dot:hover {
	opacity: .5;
}

.carousel__indicadores .glider-dot.active {
	opacity: 1;
}



.carousel__elemento{
	width: 100%;
	height: 100%;
}

.carousel__elemento img{
	width: 100%;
	height: 100%;
}

@media screen and (max-width: 1200px) {
	.carousel__anterior,
	.carousel__siguiente {
		width: 2.1875rem;
		height: 2.1875rem;
	}
	.carousel__title{
		font-size: .9rem;
		
	}
	.carousel__text{
		font-size: .8rem;
		
	}
	
	.carousel__link{
	
		font-size: .8rem;
		
	}
}

@media screen and (max-width: 800px) {
	.chuquis__title{
		padding: 2rem 0;
	}
}
@media screen and (max-width: 450px) {
	.chuquis__title{
		padding: 1.5rem 0;
	}
}
