@use '../base/variables' as var;
#bodyLogin{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../../build/img/limaPeru.webp');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.form{
    width: 35.25rem;
    height: 20rem;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.575);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.login{
    padding: 1rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    & label{
        color: var.$blanco;
        font-family: var.$bold;
        font-size: 1.3rem;
    }

    &__inp{
        border: none;
        padding: .3rem 0;
        margin-left: 1em;
        outline: none;
        color: var.$gris;
        &::placeholder{
            
            font-size: .7rem;
        }

        &:focus{
            outline: 3px solid var.$naranja;
        }
    }

    &__btn{
        padding: .8rem 1.4rem;
        border: none;
        background-color: var.$naranja;
        color: var.$blanco;
    }
}

.forget{
    width: 90%;
    height: 6.25rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    

    &__olvidar{
        padding: 1rem 0;
        text-decoration: none;
        color: rgb(199, 0, 0);
        // font-size:;
    }

    &__crear{
        color: rgb(255, 255, 255);
        text-decoration: none;
    }
}