@use '../base/variables' as var;

.album-container{
    max-width: 1600px;
    margin: 5rem auto 5rem;
}

.album{
    
    &__title{
        text-align: center;
        padding: 1rem 0;
        font-size: 1.5rem;
        font-family: var.$bold;
    }
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    &-card{
        width: 30%;
        height: 23.75rem;
        background-color: var.$blanco;
        border: 2px solid var.$verde;
        margin: .5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        &__img{
            width: 100%;
            height: 70%;
            object-fit: cover;
            border-bottom: 2px solid var.$verde;
        }
    }
}

.card-info{
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: .5rem 0;
    
    &__title{
        height: 20%;
        text-align: center;
        font-family: var.$bold;
        padding-top: .5em 0;
    }

    &__text{
        width: 90%;
        margin:  0;
        height: 60%;
        overflow: hidden;
        font-family: var.$regular;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__time{
        font-family: var.$regular;
        height: 20%;
        font-size: .9rem;
        // padding: .7em 0;
        color: var.$verde;
    }
}


@media screen and (max-width:1200px) {
    .album-card{
        width: 45%;
    }
}
@media screen and (max-width:800px) {

    .album-container{
        margin: 20rem auto 5rem;
    }

    .album-card{
        width: 70%;
    }
    // .card-info__title{
    //     font-size: .8rem;
    // }

    .card-info__text{
        font-size: .8rem;
    }
    .card-info__time{
        font-size: .8rem;
    }
    
}
@media screen and (max-width:450px) {
    .album-card{
        width: 90%;
        // height: 23rem;
    }

}