}

.carousel__anterior,
.carousel__siguiente {
  position: absolute;
  display: block;
  width: 40px;
  height: 40px;
  border: none;
  top: calc(50% - 15px);
  cursor: pointer;
  text-align: center;
  background: none;
  color: #fff;
  fill: rgba(218, 208, 208, 0.466);
}

.carousel__anterior:active,
.carousel__siguiente:active {
  fill: #fff;
}

.carousel__anterior {
  left: 5px;
}

.carousel__siguiente {
  right: 5px;
}

.carousel__lista {
  overflow: hidden;
  height: 100%;
}

.carousel__elemento {
  text-align: center;
  margin: 0 5px;
  position: relative;
}

.carousel__indicadores .glider-dot {
  display: block;
  width: 30px;
  height: 4px;
  background: #fff;
  opacity: 0.2;
  border-radius: 0;
}

.carousel__parrafo {
  position: absolute;
  bottom: 0;
  width: 100%;
  min-height: 40%;
  color: #fff;
  font-family: "InterB";
  background-color: rgba(0, 0, 0, 0.479);
  padding: 0.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.carousel__title {
  font-size: 1.2rem;
}

.carousel__text {
  font-size: 0.9rem;
  font-family: "InterM";
  padding: 0.6em;
  margin: 0;
}

.carousel__link {
  background-color: #986c43;
  text-decoration: none;
  font-family: "ManropeR";
  font-size: 0.9rem;
  padding: 0.5em 1em;
  color: black;
}

.carousel__indicadores .glider-dot:hover {
  opacity: 0.5;
}

.carousel__indicadores .glider-dot.active {
  opacity: 1;
}

.carousel__elemento {
  width: 100%;
  height: 100%;
}

