table {
  width: 100%;
  border-collapse: collapse;
  margin: 0 auto;
}

.table__title {
  text-align: center;
  padding: 0.5rem 0;
  color: #fff;
}

.header_fixed {
  max-height: 100vh;
  width: 100%;
  overflow: auto;
}

.header_fixed thead th {
  position: sticky;
  top: 0;
  background-color: #364037;
  color: #e6e7e8;
  font-size: 15px;
}

th,
td {
  border-bottom: 1px solid #dddddd;
  padding: 10px 20px;
  font-size: 14px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 60px;
  font-size: 1rem;
}

td img {
  height: 5rem;
  border: 5px solid #e6e7e8;
  object-fit: cover;
  min-width: 100%;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

tr:nth-child(odd) {
  background-color: #edeef1;
  min-width: 100%;
}

tr:hover td {
  color: #986c43;
  cursor: pointer;
  background-color: #ffffff;
}

td button {
  border: none;
  padding: 7px 20px;
  width: 48%;
  border-radius: 20px;
  background-color: #364037;
  color: #e6e7e8;
}

.table-btn {
  border-bottom: 1px solid #dddddd;
  font-size: 14px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
}
.table-btn__upd {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 0.7rem;
  cursor: pointer;
  text-decoration: none;
  padding: 0.5rem 0rem;
  background-color: #364037;
  color: #fff;
  margin: 0.1rem 0;
}
.table-btn__del {
  display: block;
  width: 100%;
  background-color: #6b0d0d;
  font-size: 0.7rem;
  cursor: pointer;
  text-decoration: none;
  padding: 0.5rem 0rem;
  text-decoration: none;
  color: #fff;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

