@use '../base/variables' as var;
.configuracion{
    width: 80%;
    height: 100%;
    margin:  0 auto;
    display: flex;
    justify-content: center;
    align-items: center;

    &-title{
        text-align: center;
        font-family: var.$bold;
        color: black;
        border-bottom: 3px solid var.$verde;
        // padding-top: .5rem;
    }
    &-form{
        width: 100%;
        height:80%;
        background-color: rgba(129, 78, 12, 0.349);
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 1rem;
        align-items: center;
        
    }

    &-info{
        color: var.$blanco
        
    }

    &-input{
        width: 10.75rem;
        color: var.$blanco;
        background-color: var.$verde;
        border: 2px solid black;
        font-size: .8rem;
    }

    &-inp{
        width: 80%;
        padding: .5rem 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        // align-items: center;

        &-foto{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 1rem 0;

            &__img{
                width: 5rem;
                height: 4rem;
                object-fit: cover;
                margin-left: 1rem;
            }

        }

        
    }

    &-lbl{
        color: black;
        font-family: var.$medium;

        &-file{
            display: block;
            max-width: 6.25rem;
            padding: .5rem 1rem;
            margin-bottom: .3rem;
            background-color: var.$verde;
            color: var.$blanco;
            text-align: center;
            cursor: pointer;
            border: 2px solid black;
            font-family: var.$regular;

           
        }
    }

    &-file{
        display: none;
    }

    &-submit{
        align-self: center;       
        padding: .5rem 1rem;
        border: none;
        font-family: var.$bold;
        cursor: pointer;
    }

    &-textArea{
        min-width: 400px;
        min-height: 200px;
        max-width: 400px;
        max-height: 200px;
        background-color: var.$verde;
        outline: none;
        border: none;
    }
}

.center{
    align-items: center;
}

.error-process{
    display: block;
    font-size: .8rem;
    color: red;
    padding-top: .5em ;
}

.success-process{
    background-color: green;
    padding: .5rem 0;
    text-align: center;
    margin: .5rem;
}

@media screen and(max-width:1000px) {
    .configuracion-form{
      
        height: 100%;
    }
}