@use '../base/variables' as var;
#bodyAdmin{
    background-color: rgba(0, 0, 0, 0.719);
}
.container{
    max-width: 1900px;
    
    height: 100%;
    margin: 0 auto;
    display: flex;
}
.settings{
    width: 20%;
    height: 100vh;
    display: flex;
    background-color: var.$verde;
    justify-content: center;
    overflow: scroll;
    &-links{
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.links-image{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

    & a img{
        width: 6.25rem;
        height: 6.375rem;
        object-fit: cover;
        border-radius: 100%;
    }

    & span{
        color: var.$blanco;
        font-family: var.$bold;
    }
}


.links-link{
    display: flex;
    flex-direction: column;
}

.link{
    display: flex;
    flex-direction: column;

    &-title{
        color: var.$naranja;
        font-size: 1.3rem;
        padding: .5em 0;
        text-align: center;
    }

    & a{
        text-decoration: none;
        color: var.$blanco;
        font-size: .9rem;
        padding: .5em 0;
        font-family: var.$regular;
    }
}

.link__salir{
    color: red;
    font-size: 1.5rem;
    background-color: red;
    text-align: center;
    padding: .5em 0;
}
.crud{
    width: 80%;
}

@media screen and (max-width:1000px) {

    
 
    .settings{
        width: 100px;
        height: 100px;
        position: fixed;
        top: 0;
        right: 0;
        z-index: 20;
        // display: none;
        //
        overflow: visible;
        background-color: transparent;

        &-link{
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    .links-image{
        width: 100%;
        height: 80%;
        display: flex;
        padding: 0;
        margin: 0;
        justify-content: center;
        align-items: center;
        & a{
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            
            & img{
                width: 100%;
                height: 100%;
                border: 3px solid var.$verde;
                margin: 0 auto;
            }
        }   

        & span{
            width: 100%;
            height: 20%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

   


    .links-link{
        // display: none;
        position: fixed;
        top: 0;
        background-color: var.$verde;
        left: 0;
        overflow:scroll;
        max-height: 100vh;
        width: 8.5rem;
        display: none;
    }

    .links-link-actived{
        display: block;
    }

    .crud{
        width: 100%;
    }

    .link a {
        font-size: .7rem;
        text-align: center;
    }
    
}