@use '../base/variables' as var;

.footer-container{
    width: 100%;
    min-height: 12.75rem;
    height: 100%;
    background-color: var.$verde;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.footer{
    max-width: 100rem;
    width: 100%;
    margin:  0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    

    &__logo{
        width: 300px;
    }

    &-links{
       width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &__link{
            padding: .5rem 0;
            text-decoration: none;
            color: var.$blanco;
            font-family: var.$medium;
            text-align: center;
            // margin-left: 1%;
        }
    }

    &-mapa iframe{
        width:300px;
        height:200px;
        border:0; 
        margin: .5rem .5rem;
    }
   
}

.copyright{
    max-width: 100rem;
    margin:  0 auto;
    padding-bottom: 1rem;
    // height: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    &__txt{
        color: var.$gris;
        margin: 0rem 2rem
    }

    &__btn{
        font-size: 2rem;
        border: none;
        background-color: var.$naranja;
        color:var.$blanco;
        padding: .5em 1em;
        cursor: pointer;
        border: 4px solid var.$naranja;

        &:active{
            background-color: transparent;
            color: var.$naranja;
        }
    }
}

@media screen and(max-width:800px) {

    .footer__logo{
        width: 200px;
    }
    .footer-mapa iframe{
        width: 12.5rem;
        height: 9.375rem;
    }

    .footer-links__link{
        font-size: .8rem;
    }

    .copyright__txt{
        font-size: .8rem;
    }
    .copyright__btn{
        font-size: 1.5rem;
    }
}
@media screen and(max-width:450px) {
    .footer__logo{
        display: none;
    }

    .footer{
        flex-wrap: wrap;
    }
    .copyright__btn{
        font-size: 1rem;
    }
}